
.app{
	width:100%;
}

body{
	font-family: 'Roboto', sans-serif;
	background: #59C173;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #5D26C1, #a17fe0, #59C173);  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #5D26C1, #a17fe0, #59C173); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

h1{
	background: #2980b9;
	color: #ffffff;
	margin: 0;
	padding: 10px 20px;
	font-size: 24px;
	font-weight: normal;
}

.taskIn{
	font-size: 18px;
	background-color: #f7f7f7;
	width: 100%;
	padding: 13px 13px 13px 20px;
	box-sizing: border-box;
	color: #2980b9;
	border-width:0px;
	border:none;
}


ul{
	list-style: none;
	margin: 0;
	padding: 0;
}

li{
	box-sizing: border-box;
	background: #ffffff;
	height: 40px;
	line-height: 40px;
	color: #666;
	padding-left:1%;
}



.taskIn:focus{
	background: white;
	border: 3px solid #2980b9;
	outline: none;
}


.completed{
	color: rgb(128, 128, 128);
	text-decoration: line-through;
}

li:hover span{
	width: 40px;
	opacity: 1.0
}

li:nth-child(2n){
	background: #f7f7f7;
}



#container{
	width:360px;
	margin: 100px auto;
	box-shadow: 0 0 3px rgba(0,0,0, 0.1);
	background: #f7f7f7;
}

.btn{
	width:100%;
	font-family: 'Times New Roman', Times, serif;
	font-size:1.2em;
	background:#2980b9;
	color:white;
	border:0;
	outline:0;
	cursor:pointer;
}

.delete{
	float:right;
	background:red;
	color:white;
	border:0;
	width:15%;
	cursor:pointer;
}
input,button:focus{
	outline:0;
}

@media screen and (max-width:800px){
	
	#container{
		width:90%;
		margin:20% auto;
	}
}

@media screen and (max-width:400px){

	#container{
		width:90%;
		margin:20% auto;
	}
}